var getRmb = require('../../common/requset.js');
export default {
	data() {
		return {
			cprop: '',
			type: '卖出',
			state: '',
			price: '',
			rmb: '',
			bsow: '',
			geunt: '',
			frte: true,
			checkall: '展示完整信息'
		};
	},
	onLoad(option) {
		var that = this;
		var bsow = JSON.parse(option.deattr);
		console.log(bsow);
		that.bsow = bsow;
		var geunt = that.bsow[1];
		that.geunt = that.bsow[1];
		that.state = bsow[0][0].order_status;
		if (that.state == 104) {
			that.state = '已完成';
		}

		that.cprop = bsow[0][0];
		that.price = bsow[0][0].sale_money;
		that.rmb = getRmb.getrmb(that.price);
	},
	methods: {
		btn: function() {
			var that = this;
			that.frte = !that.frte;
			if (that.frte == false) {
				that.checkall = '收起';
			} else {
				that.checkall = '展示完整信息';
			}
		}
	}
};
